.product-item {
  padding: 20px 0 116px;
  &__img {
    margin-top: 32px;
    img {
      width: 100%;
      border-radius: 12px;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    background: #fff;
    border-radius: 12px;
    padding: 24px;
    margin-top: 8px;
    p {
      font-size: 16px;
      line-height: 150%;
    }
  }
}
.industries2 {
  @media screen and (min-width: 1024px) {
    .swiper-wrapper {
      transform: unset !important;
      display: grid;
      grid-template-columns: repeat(5, calc(100% / 5 - 32px / 5));
      gap: 8px;
      overflow: hidden;
    }
    .swiper-slide {
      width: 100% !important;
    }
  }
}
.documents {
  padding: 28px 0;
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 16px / 3));
    gap: 8px;
    margin-top: 32px;
  }
}
.document-card {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: start;
  gap: 12px;
  background: #fff;
  border-radius: 12px;
  padding: 24px;
  &__img {
    width: 24px;
    img {
      width: 100%;
    }
  }
  &__desc {
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  &__title {
    font-size: 16px;
    line-height: 150%;
  }
  &__date {
    font-size: 16px;
    line-height: 150%;
    color: #707680;
    margin-top: 8px;
    margin-bottom: auto;
  }
  &__download {
    font-size: 16px;
    line-height: 150%;
    color: #1C468A;
    margin-top: 48px;
    span {
      display: inline-block;
      color: #707680;
      margin-left: 8px;
    }
  }
  &--disabled {
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.5);
      backdrop-filter: blur(12px);
      border-radius: 12px;
    }
  }
  &__info {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: calc(100% - 40px);
    max-height: calc(100% - 40px);
    width: max-content;
    height: max-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    .document-card__title {
      max-width: 350px;
      font-weight: 600;
      margin-top: 8px;
    }
  }
  &__icon {
    width: 18px;
    img {
      width: 100%;
    }
  }
  &__link {
    font-size: 16px;
    line-height: 150%;
    color: #1C468A;
    margin-top: 8px;
  }
}

.banner2 {
  padding: 28px 0 116px;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background: rgba(42, 49, 61, 0.2),radial-gradient(234.26% 141.42% at 100% 100%,rgb(9, 48, 108),rgb(21, 73, 153) 100%);
    border-radius: 12px;
    padding: 56px 24px;
  }
  &__title {
    font-size: 20px;
    line-height: 105%;
    font-weight: 600;
    color: #fff;
  }
  &__text {
    font-size: 16px;
    line-height: 150%;
    color: #DFE6F0;
    margin-top: 12px;
  }
  &__btn {
    height: 56px;
    gap: 15px;
    padding: 5px 24px;
    margin-top: 32px;
    span {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .industries2 {
    .swiper-wrapper {
      grid-template-columns: repeat(4, calc(25% - 24px / 4));
    }
  }
  .documents {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 8px / 2));
    }
  }
}

@media screen and (max-width: 768px) {
  .documents {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 576px) {
  .product-item {
    padding: 16px 0 32px;
    &__img {
      margin-top: 20px;
    }
    &__text {
      gap: 20px;
      padding: 20px;
      p {
        font-size: 14px;
      }
    }
  }
  .documents {
    padding: 16px 0;
    &__cards {
      margin-top: 20px;
    }
  }
  .document-card {
    padding: 20px;
    &__title {
      font-size: 14px;
    }
    &__date {
      font-size: 13px;
      margin-top: 8px;
    }
    &__download {
      font-size: 14px;
      margin-top: 20px;
    }
  }
  .banner2 {
    background: rgba(42, 49, 61, 0.2),radial-gradient(234.26% 141.42% at 100% 100%,rgb(9, 48, 108),rgb(21, 73, 153) 100%);
    border-radius: 12px;
    padding: 0;
    margin: 16px 0 32px;
    &__inner {
      background: transparent;
      border-radius: 0;
      padding: 32px 0;
    }
    &__btn {
      height: 48px;
      padding: 5px 16px;
      span {
        font-size: 14px;
      }
    }
  }
}