.career {
  padding: 20px 0 116px;
  &__subtitle {
    font-size: 16px;
    line-height: 150%;
    color: #707680;
    margin-top: 12px;
  }
  &-form {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 16px / 2));
    gap: 20px 16px;
    &__wrap {
      background: #fff;
      border-radius: 12px;
      padding: 24px;
      margin-top: 32px;
    }
    &__bottom {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 16px;
    }
    &__btn {
      flex-shrink: 0;
    }
  }
  .column--100 {
    grid-column: 1/3;
  }
}
.career-main {
  padding: 16px 116px;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
  }
  &__item {
    background: #fff;
    border-radius: 12px;
    padding: 24px;
    &-show {
      cursor: pointer;
    }
    .item-show--active {
      .career-main__item-open {
        transform: rotate(-180deg);
      }
    }
    &-group {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
    }
    &-open {
      width: 16px;
      flex-shrink: 0;
      transition: all 0.3s;
      img {
        width: 100%;
      }
    }
    &-info {
      font-size: 16px;
      line-height: 150%;
      color: #646D7A;
      margin-top: 8px;
    }
    &-hidden {
      display: none;
      margin-top: 20px;
      ul {
        display: grid;
        grid-template-columns: 100%;
        gap: 8px;
        margin-top: 8px;
        li {
          position: relative;
          padding-left: 15px;
          font-size: 16px;
          line-height: 150%;
          &:after {
            content: '';
            position: absolute;
            top: 8px;
            left: 0;
            width: 5px;
            height: 5px;
            background: #21262E;
            border-radius: 50%;
          }
        }
      }
      p {
        font-size: 16px;
        inset-inline: 150%;
        a {
          display: inline-block;
          color: #1C468A;
        }
      }
      i {
        width: 100%;
        height: 20px;
        display: block;
        opacity: 0;
        pointer-events: none;
      }
    }
    &-btns {
      display: flex;
      align-items: stretch;
      gap: 12px;
    }
    &-btn {
      height: 44px;
      padding: 5px 30px;
      span {
        font-size: 16px;
      }
    }
    &-btn2 {
      width: 44px;
      height: 44px;
      &:hover {
        svg path {
          fill: #fff;
        }
      }
      svg {
        max-width: 90%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .career {
    padding: 16px 0 32px;
    &__subtitle {
      font-size: 14px;
      margin-top: 8px;
    }
    &-form {
      grid-template-columns: 100%;
      &__wrap {
        padding: 20px;
        margin-top: 20px;
      }
      &__bottom {
        flex-direction: column;
      }
      &__btn {
        order: -1;
      }
    }
    .column--100 {
      grid-column: unset;
    }
  }
  .career-main {
    padding: 10px 0 32px;
    &__item {
      padding: 20px;
      &-info {
        font-size: 14px;
      }
      &-hidden {
        margin-top: 16px;
        ul {
          li {
            font-size: 14px;
          }
        }
        p {
          font-size: 14px;
        }
        i {
          height: 12px;
        }
      }
      &-btns {
        margin-top: 4px;
      }
      &-btn {
        span {
          font-size: 14px;
        }
      }
    }
  }
}