.alphabet {
  padding: 16px 0 116px;
  &-top {
    position: relative;
    margin-top: 32px;
    .swiper {
      overflow: unset;
    }
    .swiper-slide {
      width: auto !important;
      font-size: 20px;
      line-height: 140%;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 32px;
  }
  &__item {
    background: #fff;
    border-radius: 12px;
    padding: 24px;
  }
  &__subitems {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
    margin-top: 24px;
  }
  &__text {
    font-size: 16px;
    line-height: 150%;
    span {
      text-transform: uppercase;
    }
  }
}

@media screen and (max-width: 1023px) {
  .alphabet {
    &__subitems {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .alphabet {
    padding: 10px 0 32px;
    &-top {
      margin-top: 20px;
      .swiper-slide {
        font-size: 16px;
      }
    }
    &__items {
      gap: 20px;
    }
    &__item {
      padding: 20px;
    }
    &__subitems {
      grid-template-columns: 100%;
      gap: 16px;
      margin-top: 16px;
    }
    &__text {
      font-size: 14px;
    }
  }
}