.basket-main {
  padding: 16px 0 116px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 8px;
  }
  &__left {
    width: calc(100% - 403px);
    background: #fff;
    border-radius: 12px;
    padding: 24px;
  }
  &__right {
    width: 395px;
    background: #fff;
    border-radius: 12px;
    padding: 24px;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    margin-bottom: 24px;
  }
  .basket__cards {
    gap: 24px;
    padding: 0;
  }
  .basket-card {
    .new-price {
      font-size: 16px;
    }
    .basket-card__check {
      label {
        display: block;
        width: 20px;
        height: 20px;
        padding: 0;
      }
    }
    @media screen and (min-width: 577px) {
      &__img {
        width: 72px;
      }
      &__title {
        font-size: 16px;
      }
      &__article {
        font-size: 14px;
      }
      &__price-text {
        font-size: 16px;
      }
      .old-price {
        font-size: 16px;
      }
      &__bottom {
        margin-top: 16px;
      }
    }
  }
  .basket-card:not(:last-child) {
    border-bottom: 1px solid #DCE0E5;
    padding-bottom: 24px;
  }
  &__info {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    &-item {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      gap: 4px;
      font-size: 16px;
      line-height: 150%;
      color: #707680;
      span {
        color: #21262E;
      }
    }
  }
  .red-text {
    color: #FF2323;
  }
  &__total {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 4px;
    font-size: 20px;
    margin-top: 24px;
  }
  &__btn {
    width: 100%;
    height: 56px;
    padding: 5px 16px;
    margin-top: 24px;
    span {
      font-size: 16px;
    }
  }
  .select-all__check--active,
  .active-check {
    label:after {
      background-image: url("../img/check.svg");
      background-color: #FFE500;
      border-color: transparent;
    }
  }
}

@media screen and (max-width: 1023px) {
  .basket-main {
    &__inner {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .basket-main {
    padding: 10px 0 32px;
    &__left,
    &__right {
      padding: 20px;
    }
    .basket-card {
      position: relative;
      gap: 8px;
    }
    .basket-card__check {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
    }
    .basket-card__bottom {
      width: calc(100% + 64px);
      margin-top: 12px;
      margin-left: -64px;
    }
    &__info {
      gap: 12px;
      &-item {
        font-size: 14px;
      }
    }
    &__total {
      font-size: 16px;
      margin-top: 20px;
    }
    &__btn {
      height: 48px;
      margin-top: 20px;
      span {
        font-size: 14px;
      }
    }
  }
}