.contacts-main {
  padding: 20px 0 28px;
  .contacts__content {
    display: flex;
    align-items: stretch;
    gap: 8px;
    margin-top: 32px;
  }
  .contacts__group {
    position: static;
    width: 395px;
    height: auto;
    order: -1;
  }
  .map__wrap {
    width: calc(100% - 403px);
    min-height: unset;
    padding-bottom: 46%;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
    background: #fff;
    border-radius: 12px;
    padding: 24px;
    margin-top: 32px;
  }
  &__item {
    &-text {
      font-size: 14px;
      line-height: 150%;
      color: #707680;
      padding-left: 32px;
    }
  }
  &__email {
    display: flex;
    align-items: center;
    gap: 14px;
    &:hover {
      color: #707680;
    }
    i {
      width: 18px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        fill: currentColor;
      }
    }
    span {
      font-size: 16px;
      line-height: 150%;
    }
  }
  &__items2 {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 8px / 2));
    gap: 8px;
    margin-top: 32px;
  }
  &__item2 {
    background: #fff;
    border-radius: 12px;
    padding: 24px;
  }
  .contacts__subitems {
    margin-top: 24px;
  }
}
.connection {
  padding: 28px 0 116px;
  &__form {
    background: #fff;
    border-radius: 12px;
    padding: 24px;
    margin-top: 32px;
    &-wrap {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 24px;
    }
    &-left {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 20px 16px;
    }
    &-right {
      display: flex;
      flex-direction: column;
      gap: 20px;
    }
    @media screen and (min-width: 769px) {
      .column--100 {
        grid-column: 1/3;
      }
      .row--group {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        textarea {
          height: auto;
          flex-grow: 1;
        }
      }
    }
    &-bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 16px;
      margin-top: 24px;
    }
  }
}

@media screen and (max-width: 1023px) {
  .contacts-main {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 24px));
    }
  }
}

@media screen and (max-width: 768px) {
  .contacts-main {
    .contacts__content {
      flex-direction: column;
    }
    .contacts__group {
      width: 100%;
    }
    .map__wrap {
      width: 100%;
      order: -2;
    }
    &__items2 {
      grid-template-columns: 100%;
    }
  }
  .connection {
    &__form {
      &-wrap {
        grid-template-columns: 100%;
        gap: 20px;
      }
      &-left {
        grid-template-columns: 100%;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts-main {
    padding: 16px 0;
    .contacts__content {
      margin-top: 20px;
    }
    .map__wrap {
      min-height: 180px;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 12px;
      padding: 20px;
      margin-top: 20px;
    }
    &__item {
      &-text {
        font-size: 14px;
      }
    }
    &__email {
      span {
        font-size: 14px;
      }
    }
    &__items2 {
      margin-top: 20px;
    }
    &__item2 {
      padding: 20px;
    }
    .contacts__subitems {
      margin-top: 20px;
    }
  }
  .connection {
    padding: 16px 0 32px;
    &__form {
      padding: 20px;
      margin-top: 20px;
      &-bottom {
        flex-direction: column;
        margin-top: 20px;
      }
    }
  }
}