.about-main {
  padding: 28px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 8px;
  }
  &__desc {
    width: 395px;
    display: flex;
    flex-direction: column;
    align-items: start;
    background: #fff;
    border-radius: 12px;
    padding: 24px;
  }
  &__slider {
    position: relative;
    width: calc(100% - 403px);
  }
  .swiper {
    height: 100%;
  }
  .swiper-wrapper {
    height: 100%;
  }
  .swiper-slide {
    position: relative;
    height: 100%;
    padding-bottom: 70%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 16px;
    margin-bottom: auto;
    p {
      font-size: 16px;
      line-height: 150%;
    }
  }
  &__btn {
    height: 56px;
    gap: 15px;
    padding: 5px 24px;
    margin-top: 32px;
    span {
      font-size: 16px;
    }
  }
  .slider-nav {
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: 24px;
    transform: translate(-50%, 0);
  }
}
.about-info {
  padding: 28px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 8px;
  }
  .map-wrap {
    position: relative;
    width: calc(100% - 403px);
    padding-bottom: 47%;
  }
  .map {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
      border-radius: 12px;
    }
    .ymaps-2-1-79-inner-panes {
      border-radius: 12px;
    }
  }
  &__desc {
    width: 395px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #fff;
    border-radius: 12px;
    padding: 24px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
  }
  &__text {
    font-size: 16px;
    line-height: 150%;
    color: #707680;
    margin-top: 8px;
  }
}
.staff {
  padding: 28px 0;
  &__slider {
    position: relative;
    margin-top: 32px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-pagination {
    display: none;
  }
}
.staff-card {
  background: #fff;
  border-radius: 12px;
  &__img {
    position: relative;
    padding-bottom: 109%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px 12px 0 0;
    }
  }
  &__desc {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 24px;
  }
  &__name {
    font-size: 20px;
    line-height: 140%;
    span {
      display: block;
      text-align: center;
      font-size: 16px;
      line-height: 150%;
    }
  }
  &__post {
    font-size: 16px;
    line-height: 150%;
    color: #707680;
    margin-top: 12px;
  }
}


@media screen and (max-width: 1023px) {
  .about-main {
    .slider-nav {
      bottom: 16px;
      background: transparent;
      padding: 0;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
    .swiper-pagination {
      width: 100%;
      &-bullet {
        background: #fff;
      }
    }
  }
  .staff {
    &__slider {
      padding-bottom: 28px;
    }
    .slider-nav {
      display: none;
    }
    .swiper-pagination {
      bottom: 0;
      width: 100%;
      display: block;
    }
  }
}

@media screen and (max-width: 768px) {
  .about-main {
    &__inner {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
    }
    &__slider {
      width: 100%;
      order: -1;
    }
  }
  .about-info {
    &__inner {
      flex-direction: column;
    }
    .map-wrap {
      width: 100%;
    }
    &__desc {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .about-main {
    padding: 10px 0 16px;
    &__desc {
      padding: 20px;
    }
    &__text {
      gap: 12px;
      margin-top: 12px;
      p {
        font-size: 14px;
      }
    }
    &__btn {
      width: 100%;
      height: 48px;
      padding: 5px;
      margin-top: 20px;
      span {
        font-size: 14px;
      }
    }
  }
  .about-info {
    padding: 16px 0;
    .map-wrap {
      min-height: 180px;
    }
    &__desc {
      padding: 20px;
    }
    &__items {
      gap: 20px;
    }
    &__text {
      font-size: 14px;
    }
  }
  .staff {
    padding: 16px 0;
    &__slider {
      margin-top: 20px;
    }
    .swiper {
      overflow: unset;
    }
  }
  .staff-card {
    &__desc {
      padding: 20px;
    }
    &__name {
      font-size: 18px;
      span {
        font-size: 14px;
      }
    }
    &__post {
      font-size: 14px;
      margin-top: 8px;
    }
  }
}