.text-page {
  padding: 20px 0 116px;
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    background: #fff;
    border-radius: 12px;
    padding: 24px;
    margin-top: 32px;
    p {
      font-size: 16px;
      line-height: 150%;
    }
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 8px;
      li {
        position: relative;
        font-size: 16px;
        line-height: 150%;
        padding-left: 16px;
        &:after {
          content: '';
          position: absolute;
          top: 9px;
          left: 0;
          width: 5px;
          height: 5px;
          background: #21262E;
          border-radius: 50%;
          pointer-events: none;
        }
      }
    }
    i {
      height: 8px;
      width: 100%;
      display: block;
      background: transparent;
      color: transparent;
      padding: 0;
      margin: 0;
      opacity: 0;
      pointer-events: none;
    }
  }
}
.questions {
  padding: 20px 0 116px;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    margin-top: 32px;
  }
  &__item {
    background: #fff;
    border-radius: 12px;
    padding: 24px;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 12px;
      cursor: pointer;
    }
    .item-show--active {
      .questions__item-btn {
        transform: rotate(-180deg);
      }
    }
    &-btn {
      width: 18px;
      transition: all 0.3s;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &-hidden {
      display: none;
      margin-top: 16px;
    }
    &-text {
      p {
        font-size: 16px;
        line-height: 150%;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .text-page {
    padding: 16px 0 32px;
    &__text {
      gap: 12px;
      padding: 20px;
      margin-top: 20px;
      p {
        font-size: 14px;
      }
      ul {
        li {
          font-size: 14px;
        }
      }
      i {
        height: 4px;
      }
    }
  }
  .questions {
    padding: 16px 0 32px;
    &__items {
      margin-top: 20px;
    }
    &__item {
      padding: 20px;
      &-text {
        p {
          font-size: 14px;
        }
      }
    }
  }
}