.banner {
  padding: 20px 0 28px;
  &__inner {
    position: relative;
    background: #21262E;
    border-radius: 12px;
    padding: 32px 24px;
    overflow: hidden;
  }
  &__desc {
    max-width: 560px;
  }
  &__title {
    font-size: 16px;
    line-height: 150%;
    font-weight: 600;
    color: #fff;
  }
  &__text {
    font-size: 16px;
    line-height: 150%;
    color: #fff;
    margin-top: 12px;
  }
  &__link {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 14px;
    line-height: 150%;
    color: #FFE500;
    margin-top: 16px;
    i {
      width: 7px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__img {
    position: absolute;
    right: 150px;
    bottom: 0;
    max-width: 260px;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -430px;
      width: 530px;
      height: 530px;
      background: #1C468A;
      border-radius: 50%;
      filter: blur(156px);
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 1;
      max-width: 100%;
    }
  }
}
.catalog2 {
  &__slider {
    position: relative;
    background: #fff;
    border-radius: 12px;
    padding: 0 24px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    &-active {
      &:after {
        opacity: 1;
      }
      .catalog2-card__title {
        color: #21262E;
      }
    }
  }
  &-card {
    position: relative;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 16px 0;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 100%;
      height: 4px;
      background: #1C468A;
      opacity: 0;
      transition: all 0.125s;
    }
    &__img {
      width: 56px;
      margin-bottom: auto;
      img {
        width: 100%;
      }
    }
    &__desc {
      max-width: 220px;
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 5px auto 0;
    }
    &__title {
      font-size: 14px;
      line-height: 150%;
      color: #707680;
    }
  }
}


@media screen and (max-width: 1100px) {
  .banner {
    &__img {
      right: 24px;
    }
  }
}

@media screen and (max-width: 1023px) {
  .banner {
    &__desc {
      max-width: 400px;
    }
  }
}

@media screen and (max-width: 768px) {
  .banner {
    &__inner {
      display: flex;
      flex-direction: column;
      gap: 20px;
      padding: 32px 24px 0 24px;
    }
    &__img {
      position: static;
      margin: 0 auto;
      &:after {
        left: unset;
        right: -400px;
        bottom: -400px;
      }
    }
  }
  .catalog2 {
    &__slider {
      background: transparent;
      padding: 0;
    }
    .swiper {
      overflow: unset;
    }
    .swiper-slide {
      width: auto !important;
    }
    &-card {
      background: #fff;
      padding: 16px 6px;
      margin: 0 !important;
      &:first-child {
        border-radius: 12px 0 0 12px;
        padding-left: 20px;
      }
      &:last-child {
        border-radius: 0 12px 12px 0;
        padding-right: 20px;
      }
      &:after {
        width: calc(100% - 12px);
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .banner {
    padding: 16px 0;
    &__inner {
      padding: 20px 20px 0 20px;
    }
    &__title {
      font-size: 14px;
    }
    &__text {
      font-size: 14px;
      margin-top: 8px;
    }
    &__link {
      font-size: 14px;
      margin-top: 12px;
    }
    &__img {
      max-width: 180px;
    }
  }
  .catalog2 {
    &-card {
      flex-direction: row;
      text-align: start;
      gap: 8px;
      &__img {
        width: 40px;
        flex-direction: column;
        margin: 0;
      }
      &__desc {
        max-width: 100%;
        margin: 0;
      }
      &__title {
        max-width: 160px;
        font-size: 13px;
      }
    }
  }
}