.mistake {
  padding: 56px 0 116px;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__404 {
    font-size: 88px;
    line-height: 127%;
    font-weight: 600;
  }
  &__title {
    margin-top: 32px;
  }
  &__text {
    font-size: 16px;
    line-height: 140%;
    color: #707680;
    margin-top: 12px;
  }
  &__btn {
    height: 56px;
    padding: 5px 24px;
    margin-top: 32px;
    span {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 576px) {
  .mistake {
    padding: 32px 0;
    &__404 {
      font-size: 56px;
    }
    &__title {
      margin-top: 20px;
    }
    &__text {
      font-size: 14px;
      margin-top: 8px;
    }
    &__btn {
      height: 48px;
      padding: 5px 16px;
      margin-top: 20px;
      span {
        font-size: 14px;
      }
    }
  }
}