h2 {
  font-size: 28px;
  line-height: 140%;
  font-weight: 600;
}
h3 {
  font-size: 20px;
  line-height: 140%;
  font-weight: 600;
}
.btn-yellow {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #FFE500;
  border-radius: 8px;
  &:hover {
    background: #FFED4D;
  }
  i {
    max-width: 20px;
    display: flex;
    align-items: center;
    img {
      max-width: 100%;
    }
  }
  span {
    line-height: 150%;
    color: #21262E;
  }
}
.btn-outline {
  color: #21262E;
  background: transparent;
  border: 1px solid #A7ABB2;
  border-radius: 8px;
  &:hover {
    background: #21262E;
    border-color: #21262E;
    color: #fff;
  }
  span {
    line-height: 150%;
  }
}
.basket {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    border-bottom: 1px solid #DCDFE5;
    padding: 20px;
  }
  &__title {
    font-size: 16px;
    line-height: 150%;
    font-weight: 600;
  }
  &__close {
    width: 15px;
    background: transparent;
    padding: 0;
    &:hover {
      svg path {
        fill: #21262E;
      }
    }
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    padding: 20px;
  }
  &-card {
    display: flex;
    align-items: start;
    gap: 12px;
    &__img {
      width: 56px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &__desc {
      flex-grow: 1;
    }
    &__top {
      display: flex;
      justify-content: space-between;
      align-items: start;
      gap: 8px;
    }
    &__title {
      font-size: 14px;
      line-height: 150%;
    }
    &__delete {
      width: 20px;
      flex-shrink: 0;
      background: transparent;
      padding: 0;
      img {
        width: 100%;
      }
    }
    &__article {
      font-size: 13px;
      line-height: 150%;
      color: #707680;
      margin-top: 4px;
    }
    &__bottom {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      margin-top: 12px;
    }
    .cartcalc {
      width: 88px;
      height: 40px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 2px;
      background: #F2F4F7;
      border-radius: 8px;
      padding: 5px 8px;
      input {
        width: 28px;
        height: 24px;
        flex-shrink: 0;
        text-align: center;
        background: transparent;
        border: transparent;
        font-size: 14px;
        line-height: 150%;
        color: #21262E;
      }
    }
    .ccalc-minus,
    .ccalc-plus {
      width: 16px;
      height: 16px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      transition: all 0.3s;
      &:hover {
        svg path {
          stroke: #21262E;
        }
      }
      svg {
        width: 11px;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    &__price {
      display: flex;
      align-items: center;
      gap: 8px;
    }
    &__price-text {
      font-size: 14px;
      color: #707680;
    }
    .new-price {
      font-size: 14px;
      line-height: 150%;
      font-weight: 600;
    }
    .old-price {
      font-size: 14px;
      line-height: 150%;
      text-decoration: line-through;
      color: #707680;
    }
  }
  &__bottom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    border-top: 1px solid #DCDFE5;
    padding: 20px;
    margin-top: 20px;
  }
  &__total {
    font-size: 16px;
    line-height: 150%;
    span {
      font-weight: 600;
    }
  }
  &__btn {
    height: 40px;
    padding: 5px 16px;
    span {
      font-size: 14px;
    }
  }
}
.breadcrumbs {
  padding: 24px 0;
  .container {
    padding: 0;
  }
  &__slider {
    position: relative;
  }
  .swiper {
    overflow: unset;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    position: relative;
    width: auto !important;
    height: auto;
    &:first-child {
      margin-left: 30px;
    }
    &:last-child {
      padding-right: 32px;
      margin-right: 0 !important;
    }
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -20px;
        transform: translate(0, -50%);
        width: 5px;
        height: 10px;
        background-image: url("../img/arrow-right.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        pointer-events: none;
      }
    }
  }
  &__item {
    font-size: 14px;
    line-height: 140%;
    color: #707680;
    &:hover {
      color: #21262E;
      svg path {
        stroke: #21262E;
      }
    }
    svg path {
      transition: all 0.3s;
    }
  }
  &__item--active {
    color: #21262E;
  }
}
.page-top {
  padding: 20px 0 16px;
  &__group,
  &__group2 {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 12px 16px;
  }
  .row-group {
    flex-wrap: nowrap;
    flex-direction: row;
  }
  &__title {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    gap: 4px 16px;
  }
  .search-result {
    color: #707680;
  }
  &__info {
    font-size: 16px;
    line-height: 150%;
    color: #707680;
    margin-bottom: 4px;
  }
  .search {
    position: relative;
    flex-shrink: 0;
    input {
      width: 320px;
      height: 40px;
      background: #fff;
      border: 1px solid transparent;
      border-radius: 8px;
      padding: 0 14px 0 44px;
      font-size: 14px;
      line-height: 170%;
      color: #21262E;
      &::placeholder {
        color: #707680;
      }
    }
    input[type="search"]::-webkit-search-cancel-button {
      -webkit-appearance: none;
      width: 15px;
      height: 15px;
      background-image: url("../img/search-icon.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      cursor: pointer;
    }
    &-btn {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 14px;
      transform: translate(0, -50%);
      width: 20px;
      background: transparent;
      padding: 0;
      svg {
        width: 20px;
        height: auto;
      }
    }
  }
  .search2 {
    margin-top: 32px;
    input {
      width: 100%;
      height: 56px;
    }
  }
  &__group2 {
    align-items: center;
    background: #fff;
    border-radius: 12px;
    padding: 16px 24px;
    margin-top: 32px;
  }
  &__selections {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: 8px;
  }
  &__select {
    position: relative;
    &--active {
      .page-top__select-hidden {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &-show {
      position: relative;
      display: flex;
      align-items: center;
      gap: 8px;
      background: #F2F4F7;
      border-radius: 4px;
      padding: 4px 12px;
      cursor: pointer;
      &:after {
        content: '';
        position: absolute;
        bottom: -8px;
        left: 0;
        width: 100%;
        height: 8px;
        background: transparent;
      }
      i {
        width: 10px;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        svg {
          width: 100%;
          height: auto;
          transition: all 0.3s;
        }
        svg path {
          transition: all 0.3s;
        }
      }
      span {
        font-size: 14px;
        line-height: 170%;
      }
    }
    &-hidden {
      position: absolute;
      z-index: 3;
      top: calc(100% + 8px);
      left: 0;
      width: 320px;
      background: #fff;
      box-shadow: 0px 12px 24px 0px rgba(33, 38, 46, 0.16);
      padding: 20px 16px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
  }
  &__checkboxes {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
  }
  &__checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-color: #FFE500;
        background-image: url("../img/check.svg");
        border-color: transparent;
      }
    }
    label {
      position: relative;
      display: block;
      font-size: 14px;
      line-height: 170%;
      padding-left: 30px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 1.5px solid #A7ABB2;
        border-radius: 4px;
        transition: all 0.3s;
      }
    }
  }
  &__select2 {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__select2-title {
    font-size: 14px;
    line-height: 140%;
    color: #707680;
  }
  .nice-select {
    width: auto;
    height: auto;
    display: flex;
    align-items: center;
    gap: 8px;
    background: transparent;
    border: none;
    padding: 0 18px 0 0;
    font-size: 14px;
    line-height: 140%;
    font-weight: 400;
    color: #21262E;
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 10px;
      height: 5px;
      background-image: url("../img/arrow-bottom.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    .list {
      width: 320px;
      left: unset;
      right: 0;
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      background: #fff;
      box-shadow: 0px 12px 24px 0px rgba(33, 38, 46, 0.16);
      padding: 20px 16px;
      margin-top: 10px;
      li {
        background: transparent !important;
        border: none;
        padding: 0;
        font-size: 14px;
        line-height: 170%;
        font-weight: 400;
        &:hover {
          color: #707680;
        }
      }
    }
  }
  &__filter-btn {
    width: 20px;
    flex-shrink: 0;
    display: none;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__selections-title {
    display: none;
    font-size: 14px;
    line-height: 170%;
    font-weight: 600;
    margin-bottom: 8px;
  }
  &__selections-bottom {
    position: relative;
    display: none;
    padding: 20px 0 48px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: calc(100% + 60px);
      height: 1px;
      background: #DCE0E5;
    }
  }
  &__selections-btn {
    height: 48px;
    padding: 5px 40px;
    margin: 0 20px;
    span {
      font-size: 14px;
    }
  }
  &__btns {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    background: #fff;
    border-radius: 8px;
  }
  &__btn {
    height: 40px;
    width: 40px;
    background: #fff;
    padding: 0;
    &:first-child {
      border-radius: 8px 0 0 8px;
    }
    &:last-child {
      border-radius: 0 8px 8px 0;
    }
    svg {
      max-width: 90%;
      width: auto;
      height: auto;
    }
  }
  .catalog2 {
    margin-top: 32px;
  }
}
.filter-bg {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.25);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
  }
}
.slider-nav {
  min-width: 80px;
  flex-shrink: 0;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  background: #fff;
  border-radius: 8px;
  padding: 12.5px 11px;
  .swiper-button-prev,
  .swiper-button-next,
  .swiper-pagination {
    position: static;
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 18px;
    &:hover {
      svg path {
        fill: #21262E;
      }
    }
    svg {
      width: 100%;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
  .swiper-pagination {
    width: auto;
  }
}
.swiper-button-prev,
.swiper-button-next {
  z-index: 1;
  width: auto;
  height: auto;
  margin-top: 0;
  &:after {
    display: none;
  }
}
.swiper-pagination {
  z-index: 1;
  &-bullet {
    width: 8px;
    height: 8px;
    background: #21262E;
    opacity: 0.5;
    &-active {
      opacity: 1;
    }
  }
}
.swiper-pagination-fraction {
  font-size: 12px;
}
.slider-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
}
.title-group {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  gap: 4px 16px;
}
.title-link {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 16px;
  line-height: 150%;
  color: #1C468A;
  margin-bottom: 3px;
  i {
    width: 14px;
    display: flex;
    align-items: center;
    svg {
      width: 100%;
      height: auto;
    }
  }
}
.number {
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F2F4F7;
  border-radius: 4px;
  span {
    font-size: 12px;
    line-height: 120%;
    font-weight: 500;
  }
}
.equipment2 {
  background: #fff;
  border-radius: 12px;
  padding: 24px;
  margin-top: 8px;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 48px;
    margin-top: 24px;
  }
  &__item {
    position: relative;
    display: flex;
    justify-content: space-between;
    gap: 12px;
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -24px;
        width: 100%;
        height: 1px;
        background: #DCE0E5;
        pointer-events: none;
      }
    }
    &-img {
      width: 56px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-desc {
      flex-grow: 1;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 12px;
    }
    &-title {
      font-size: 16px;
      line-height: 150%;
    }
    &-article {
      font-size: 16px;
      line-height: 150%;
      color: #707680;
      margin-top: 8px;
    }
  }
  &__price {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 8px;
    span:nth-child(1) {
      font-size: 16px;
      line-height: 150%;
      font-weight: 600;
    }
    span:nth-child(2) {
      font-size: 14px;
      line-height: 150%;
      text-decoration: line-through;
      color: #707680;
    }
    &-text {
      flex-shrink: 0;
      font-size: 16px;
      line-height: 150%;
      color: #707680;
    }
  }
}
.modal {
  background: rgba(#000, .25);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 1024px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    max-width: 680px;
    width: 100%;
    background: #fff;
    border-radius: 12px;
    margin: 0 auto;
    padding: 24px;
    @media screen and (max-width: 1023px) {
      margin: 50px auto !important;
    }
    @media screen and (max-width: 768px) {
      max-width: calc(100% - 40px) !important;
    }
  }
  &__dialog2 {
    max-width: 540px;
    padding: 72px 24px 32px;
  }
  &__dialog3 {
    max-width: 540px;
  }
  &__content2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__top {
    position: relative;
    padding-bottom: 24px;
    padding-right: 32px;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: calc(100% + 48px);
      height: 1px;
      background: #A7ABB2;
      pointer-events: none;
    }
  }
  &__close {
    position: absolute;
    z-index: 2;
    top: 30px;
    right: 24px;
    width: 15px;
    background: transparent;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__title {
    display: flex;
    align-items: center;
    gap: 15px;
    .title-arrow {
      width: 18px;
      cursor: pointer;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &-form {
    margin-top: 24px;
    &__grid {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 20px 16px;
    }
    &__info {
      margin-top: 20px;
    }
    &__bottom {
      position: relative;
      padding-top: 24px;
      margin-top: 74px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        width: calc(100% + 48px);
        height: 1px;
        background: #A7ABB2;
        pointer-events: none;
      }
    }
    &__btn {
      margin-left: auto;
    }
  }
  .column--100 {
    grid-column: 1/3;
  }
  &__img {
    max-width: 192px;
    margin-bottom: 24px;
    img {
      width: 100%;
    }
  }
  &__subtitle {
    font-size: 16px;
    line-height: 150%;
    color: #707680;
    margin-top: 8px;
  }
  &__btn {
    height: 56px;
    padding: 5px 47px;
    margin-top: 24px;
    span {
      font-size: 16px;
    }
  }

}
.form {
  &-group {
    position: relative;
    .input-title {
      display: block;
      font-size: 13px;
      line-height: 140%;
      color: #707680;
      margin-bottom: 8px;
    }
    .password-input {
      position: relative;
    }
    .password-btn {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 16px;
      transform: translate(0, -50%);
      width: 22px;
      cursor: pointer;
      &:hover {
        svg path {
          transition: all 0.3s;
          fill: #21262E;
        }
      }
      svg {
        width: 100%;
        height: auto;
      }
    }
    input {
      width: 100%;
      height: 56px;
      background: #F2F4F7;
      border: 1px solid transparent;
      border-radius: 8px;
      padding: 0 16px;
      font-size: 16px;
      line-height: 150%;
      &::placeholder {
        color: #A1A7B2;
      }
    }
    textarea {
      resize: none;
      width: 100%;
      height: 112px;
      background: #F2F4F7;
      border: 1px solid transparent;
      border-radius: 8px;
      padding: 16px;
      font-size: 16px;
      line-height: 150%;
      &::placeholder {
        color: #A1A7B2;
      }
    }
  }
  .nice-select {
    width: 100%;
    height: 56px;
    display: flex;
    align-items: center;
    background: #F2F4F7 !important;
    border: 1px solid transparent;
    border-radius: 8px;
    padding: 0 48px 0 16px;
    font-size: 16px;
    line-height: 150%;
    color: #A1A7B2;
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 50%;
      right: 16px;
      transform: translate(0, -50%);
      width: 10px;
      height: 5px;
      background-image: url("../img/arrow-bottom.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    .list {
      width: 100%;
      display: grid;
      grid-template-columns: 100%;
      gap: 16px;
      background: #fff;
      box-shadow: 0px 12px 24px 0px rgba(33, 38, 46, 0.16);
      padding: 20px 16px;
      margin-top: 10px;
      li {
        background: transparent !important;
        border: none;
        padding: 0;
        font-size: 14px;
        line-height: 170%;
        font-weight: 400;
        &:hover {
          color: #707680;
        }
      }
    }
    .active {
      color: #21262E;
    }
  }
  .open:after {
    transform: rotate(-180deg);
  }
  &-info {
    font-size: 13px;
    line-height: 140%;
    color: #707680;
    a {
      color: #1C468A;
    }
  }
  &-btn {
    height: 56px;
    padding: 5px 35px;
    span {
      font-size: 16px;
    }
  }
  .file-wrap {
    position: relative;
    background: transparent;
    border: 1px solid #C9CACC;
    border-radius: 12px;
    padding: 32px 24px;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
    }
  }
  .file-desc {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .file-title {
    font-size: 14px;
    line-height: 150%;
    font-weight: 500;
  }
  .file-text {
    font-size: 14px;
    line-height: 150%;
    color: #707680;
    margin-top: 8px;
  }
  .file-btn {
    height: 44px;
    gap: 8px;
    border: 1px solid #A7ABB2;
    border-radius: 8px;
    padding: 5px 16px;
    margin-top: 16px;
    i {
      width: 20px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 14px;
      line-height: 150%;
    }
  }
  #files-area {
    position: relative;
    display: flex;
    align-items: center;
    text-align: center;
    font-size: 14px;
    line-height: 150%;
    color: #707680;
    padding-top: 12px;
  }
  .file-block {
    display: flex;
    align-items: start;
    gap: 5px;
  }
  .file-delete {
    width: 18px;
    height: 20px;
    order: 1;
    background-image: url("../img/delete.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    cursor: pointer;
    span {
      opacity: 0;
    }
  }
  .file-block {
    .name {
      position: relative;
      padding-left: 28px;
      &:after {
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 16px;
        height: 19px;
        background-image: url("../img/file.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
  }
  &-flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 6px 12px;
  }
  &-check {
    position: relative;
    display: inline-block;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
        background-color: #FFE500;
        border-color: transparent;
      }
    }
    label {
      position: relative;
      padding-left: 30px;
      font-size: 14px;
      line-height: 150%;
      &:after {
        content: '';
        position: absolute;
        top: -2px;
        left: 0;
        width: 20px;
        height: 20px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 1.5px solid #A7ABB2;
        border-radius: 4px;
      }
    }
  }
  .forgot-password {
    font-size: 14px;
    line-height: 150%;
    color: #1C468A;
    cursor: pointer;
  }
}
.login {
  &__subtitle {
    font-size: 14px;
    line-height: 150%;
    color: #707680;
    margin-top: 8px;
  }
  &__tabs-head {
    display: grid;
    grid-template-columns: repeat(2, calc(50%));
    margin-top: 24px;
  }
  .tab {
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border: 1px solid #A7ABB2;
    font-size: 14px;
    line-height: 150%;
    color: #707680;
    cursor: pointer;
    &:first-child {
      border-radius: 8px 0 0 8px;
      border-right: unset;
    }
    &:last-child {
      border-left: unset;
      border-radius: 0 8px 8px 0;
    }
   }
  .tab--active {
    background: #FFE500;
    border-color: transparent;
    color: #21262E;
  }
  &__tabs-body {
    margin-top: 24px;
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &-form {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &__btns {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-top: 4px;
  }
}
.registration {
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 25px;
    margin-top: 24px;
  }
  &__tabs-head {
    width: 165px;
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 16px;
  }
  .tab {
    display: flex;
    align-items: center;
    gap: 12px;
    color: #A7ABB2;
    cursor: pointer;
    i {
      width: 24px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      svg {
        max-width: 100%;
        width: auto;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
        fill: currentColor;
      }
    }
    span {
      font-size: 14px;
      line-height: 150%;
    }
  }
  .tab--active {
    color: #21262E;
  }
  &__tabs-body {
    width: calc(100% - 190px);
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &-form {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 24px;
  }
  &-form__bottom {
    position: relative;
    padding-top: 24px;
    margin-top: 24px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: -24px;
      width: calc(100% + 214px);
      height: 1px;
      background: #DCDFE5;
      pointer-events: none;
    }
  }
  &__btn {
    margin-top: 0;
    margin-left: auto;
  }
}



@media screen and (max-width: 1023px) {
  .page-top{
    &__selections-wrap {
      position: fixed;
      z-index: 5;
      left: 0;
      bottom: -105%;
      width: 100%;
      height: calc(100% - 150px);
      background: #fff;
      border-radius: 12px;
      padding: 28px 30px 116px 30px;
      pointer-events: none;
      transition: all 0.5s;
      overflow-x: hidden;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #A7ABB2;
        border-radius: 3px;
        margin: 6px 0;
      }
      &::-webkit-scrollbar-thumb {
        background: #FFE500;
        border-radius: 3px;
        margin: 6px 0;
      }
      &:after {
        content: '';
        position: absolute;
        top: 12px;
        left: 50%;
        transform: translate(-50%, 0);
        width: 24px;
        height: 4px;
        background: #A7ABB2;
        border-radius: 24px;
      }
    }
    .filter--active {
      bottom: 0;
      pointer-events: auto;
      .page-top__selections-bottom {
        bottom: 0;
      }
    }
    &__selections-title {
      display: block;
    }
    &__selections {
      flex-direction: column;
    }
    &__select {
      width: 100%;
      &:not(:last-child) {
        border-bottom: 1px solid #DCE0E5;
      }
      &--active {
        .page-top__select-show {
          i svg {
            transform: rotate(-180deg);
          }
        }
        .page-top__select-hidden {
          height: auto;
          margin: 8px 0 12px 0;
          overflow: unset;
        }
      }
      &-show {
        width: 100%;
        justify-content: space-between;
        background: transparent;
        border-radius: 0;
        padding: 12px 0;
      }
      &-hidden {
        position: static;
        width: 100%;
        height: 0;
        background: transparent;
        box-shadow: unset;
        padding: 0;
        opacity: 1;
        pointer-events: auto;
        overflow: hidden;
      }
    }
    &__selections-bottom {
      position: fixed;
      left: 0;
      bottom: -105%;
      width: 100%;
      display: flex;
      justify-content: center;
      background: #fff;
      border-radius: 0 0 12px 12px;
      transition: all 0.5s;
      &:before {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 12px;
        transform: translate(-50%, 0);
        width: 108px;
        height: 4px;
        background: #A7ABB2;
        border-radius: 14px;
      }
    }
    &__filter-btn {
      display: block;
    }
  }
}

@media screen and (max-width: 768px) {
  .breadcrumbs {
    .swiper-slide {
      &:first-child {
        margin-left: 20px;
      }
      &:last-child {
        padding-right: 32px;
