.main {
  padding: 56px 0 28px;
  &__inner {
    position: relative;
  }
  &__slider {
    position: relative;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    position: relative;
    height: auto;
    min-height: 500px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding-bottom: 47%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px;
    }
  }
  &__desc {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    max-width: 443px;
    height: 100%;
    padding: 24px;
  }
  &__group {
    width: 100%;
    height: 100%;
    display: inline-flex;
    flex-direction: column;
    align-items: start;
    background: #fff;
    border-radius: 8px;
    padding: 24px;
  }
  &__tag {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: #F2F4F7;
    border-radius: 4px;
    padding: 8px 12px;
    font-size: 12px;
    line-height: 130%;
    font-weight: 500;
    text-transform: uppercase;
  }
  &__title {
    margin-top: 24px;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 150%;
    margin-top: 16px;
    margin-bottom: auto;
  }
  &__btn {
    height: 56px;
    gap: 15px;
    padding: 5px 22px;
    margin-top: 16px;
    span {
      font-size: 16px;
    }
  }
  .slider-nav {
    position: absolute;
    z-index: 2;
    right: 24px;
    bottom: 24px;
  }
}
.equipment {
  padding: 28px 0;
  &__cards {
    position: relative;
    display: grid;
    grid-template-columns: repeat(6, calc(100% / 6 - 40px / 6));
    gap: 8px;
    margin-top: 32px;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 88px;
      background: linear-gradient(0.00deg, rgb(242, 244, 247),rgba(242, 244, 247, 0) 100%);
      pointer-events: none;
    }
  }
  &__btn {
    height: 56px;
    padding: 5px 28px;
    margin: 32px auto 0;
    span {
      font-size: 16px;
    }
  }
}
.equipment-card {
  position: relative;
  min-height: 188px;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 12px;
  background: #fff;
  border-radius: 12px;
  padding: 24px 12px 24px 24px;
  @media screen and (min-width: 1024px) {
    &:nth-child(1) {
      grid-column: 1/5;
      grid-row: 1/3;
    }
    &:nth-child(2) {
      grid-column: 5/7;
    }
    &:nth-child(3) {
      grid-column: 5/7;
    }
    &:nth-child(4) {
      grid-column: 1/4;
    }
    &:nth-child(5) {
      grid-column: 4/7;
    }
  }
  &__desc {
    display: flex;
    flex-direction: column;
  }
  &__img {
    max-width: 312px;
    flex-shrink: 0;
    display: flex;
    align-items: flex-end;
    img {
      max-width: 100%;
    }
  }
  &__title {
    font-size: 16px;
    line-height: 150%;
    font-weight: 600;
    margin-bottom: 8px;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 150%;
    color: #707680;
    margin-bottom: 8px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient: vertical;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    margin: 24px 0 8px;
    p {
      font-size: 14px;
      line-height: 150%;
    }
  }
  &__link {
    font-size: 16px;
    line-height: 150%;
    color: #1C468A;
    margin-top: auto;
  }
  &__number {
    position: absolute;
    top: 24px;
    right: 24px;
  }
}
.industries {
  padding: 28px 0;
  &__slider {
    position: relative;
    margin-top: 32px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    height: auto;
  }
  .swiper-pagination {
    display: none;
  }
}
.industry-card {
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: start;
  background: #fff;
  border-radius: 12px;
  padding: 24px;
  &__img {
    position: relative;
    height: 178px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto auto;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 40%;
      transform: translate(-50%, -50%) rotate(-45deg);
      width: 73px;
      height: 73px;
      background: #D9D9D9;
      pointer-events: none;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 40%;
      transform: translate(50%, -50%) rotate(-45deg);
      width: 73px;
      height: 73px;
      background: #D9D9D9;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 2;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    align-items: flex-end;
    margin-top: 32px;
  }
  &__title {
    font-size: 16px;
    line-height: 150%;
    &:hover {
      color: #1C468A;
    }
  }
  &__number {
    position: absolute;
    top: 24px;
    left: 24px;
  }
}
.about {
  padding: 28px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 8px;
  }
  &__img {
    position: relative;
    width: 50%;
    padding-bottom: 47%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px;
    }
  }
  &__desc {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: start;
    background: #fff;
    border-radius: 12px;
    padding: 24px;
  }
  &__title {
    margin-bottom: 16px;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-bottom: 24px;
    p {
      font-size: 16px;
      line-height: 150%;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-bottom: 24px;
  }
  &__item {
    &-title {
      font-size: 22px;
      line-height: 150%;
      margin-bottom: 4px;
    }
    &-text {
      p {
        font-size: 14px;
        line-height: 150%;
        color: #707680;
      }
    }
  }
  &__btn {
    height: 56px;
    gap: 15px;
    padding: 5px 26px;
    margin-top: auto;
    span {
      font-size: 16px;
    }
  }
}
.news {
  padding: 28px 0;
  &2 {
    padding-bottom: 116px;
  }
  &__slider {
    position: relative;
    margin-top: 32px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-pagination {
    display: none;
  }
}
.news-card {
  position: relative;
  height: auto;
  background: #fff;
  border-radius: 12px;
  &__img {
    position: relative;
    padding-bottom: 56%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px 12px 0 0;
    }
  }
  &__desc {
    border-radius: 0 0 12px 12px;
    padding: 24px;
  }
  &__date {
    font-size: 13px;
    line-height: 150%;
    color: #707680;
  }
  &__title {
    font-size: 16px;
    line-height: 150%;
    margin-top: 12px;
  }
  &__text {
    margin-top: 12px;
    p {
      font-size: 16px;
      line-height: 150%;
      color: #707680;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:2;
      -webkit-box-orient: vertical;
    }
  }
  &__tag {
    position: absolute;
    z-index: 2;
    top: 24px;
    left: 24px;
    background: #F2F4F7;
    border-radius: 8px;
    padding: 8px 16px;
    span {
      font-size: 12px;
      line-height: 120%;
      font-weight: 500;
    }
  }
}
.contacts {
  padding: 28px 0 112px;
  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 20px 16px;
  }
  &__tabs-head {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
  }
  @media screen and (min-width: 1024px) {
    .swiper {
      margin: 0;
    }
    .swiper-wrapper {
      transform: unset !important;
    }
  }
  .swiper-slide {
    width: auto !important;
  }
  .tab {
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    padding: 5px 16px;
    font-size: 14px;
    line-height: 170%;
    color: #707680;
    cursor: pointer;
    &:first-child {
      border-radius: 8px 0 0 8px;
    }
    &:last-child {
      border-radius: 0 8px 8px 0;
    }
    &--active {
      background: #FFE500;
      color: #21262E;
    }
  }
  &__tabs-body {
    margin-top: 32px;
  }
  &__content {
    position: relative;
  }
  .tab__content {
    display: none;
  }
  .tab__content--active {
    display: block;
  }
  .map__wrap {
    position: relative;
    min-height: 600px;
    padding-bottom: 53%;
  }
  #map,
  #map2,
  #map3,
  #map4 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
      border-radius: 12px;
    }
    .ymaps-2-1-79-inner-panes {
      border-radius: 12px;
    }
  }
  &__desc {
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;
    max-width: 443px;
    width: 100%;
    height: 100%;
    padding: 24px;
  }
  &__group {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: start;
    background: #fff;
    border-radius: 12px;
    padding: 24px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 24px;
    margin-bottom: 24px;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 150%;
    font-weight: 600;
  }
  &__subitems {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-top: 16px;
  }
  &__subitem {
    display: flex;
    align-items: center;
    gap: 6px 12px;
    &:hover {
      color: #1C468A;
    }
    &-icon {
      width: 24px;
      svg {
        max-width: 100%;
        width: auto;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
        fill: currentColor;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 150%;
    }
  }
  &__subitem-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 5px;
    &:hover {
      color: #1C468A;
    }
  }
  &__btns {
    display: flex;
    align-items: stretch;
    gap: 12px;
    margin-top: auto;
  }
  &__btn {
    height: 56px;
    padding: 5px 30px;
    span {
      font-size: 16px;
    }
  }
  &__btn2 {
    width: 56px;
    height: 56px;
    background: transparent;
    border: 1px solid #A7ABB2;
    border-radius: 8px;
    svg {
      max-width: 90%;
      width: auto;
      height: auto;
    }
  }
  .photo-office {
    position: absolute;
    z-index: 2;
    top: 24px;
    right: 24px;
    display: flex;
    align-items: center;
    gap: 12px;
    background: #fff;
    box-shadow: 0px 12px 24px 0px rgba(33, 38, 46, 0.16);
    border-radius: 8px;
    padding: 6px 12px 6px 6px;
    cursor: pointer;
    &__img {
      position: relative;
      width: 44px;
      height: 44px;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 4px;
      }
    }
    &__title {
      font-size: 16px;
      line-height: 150%;
    }
  }
  &__btn-map {
    background: transparent;
    padding: 0;
    font-size: 16px;
    line-height: 150%;
    color: #1C468A;
    margin-top: 24px;
  }
}

@media screen and (max-width: 1100px) {
  .equipment-card {
    &__subtitle {
      -webkit-line-clamp:2;
    }
  }
}

@media screen and (max-width: 1023px) {
  .equipment {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 8px / 2));
    }
  }
  .equipment-card {
    &:nth-child(1) {
      grid-column: 1/3;
    }
  }
  .industries {
    &__slider {
      padding-bottom: 30px;
    }
    .slider-nav {
      display: none;
    }
    .swiper-pagination {
      bottom: 0;
      display: block;
    }
  }
  .industry-card {
    padding: 20px;
    &__title {
      font-size: 14px;
    }
    .number {
      top: 20px;
      left: 20px;
    }
  }
  .about {
    &__desc {
      padding: 20px;
    }
    &__title {
      margin-bottom: 12px;
    }
    &__text {
      gap: 12px;
      margin-bottom: 20px;
      p {
        font-size: 14px;
      }
    }
    &__items {
      gap: 12px;
      margin-bottom: 20px;
    }
    &__item {
      &-title {
        font-size: 20px;
      }
    }
  }
  .news {
    &__slider {
      padding-bottom: 30px;
    }
    .slider-nav {
      display: none;
    }
    .swiper-pagination {
      bottom: 0;
      display: block;
    }
  }
  .news-card {
    &__desc {
      padding: 20px;
    }
    &__title {
      font-size: 14px;
      margin-top: 8px;
    }
    &__text {
      margin-top: 8px;
      p {
        font-size: 14px;
      }
    }
  }
  .contacts {
    &__top {
      display: block;
    }
    &__tabs-head {
      width: 100%;
      justify-content: start;
      margin-top: 20px;
    }
    .swiper {
      width: 100%;
      overflow: unset;
    }
  }
}

@media screen and (max-width: 768px) {
  .equipment {
    &__cards {
      &:after {
        height: 56px;
      }
    }
  }
  .equipment-card {
    &__img {
      max-width: 200px;
    }
  }
  .about {
    &__inner {
      flex-direction: column;
    }
    &__img {
      width: 100%;
    }
    &__desc {
      width: 100%;
    }
  }
  .contacts {
    .map__wrap {
      min-height: 180px;
    }
    &__desc {
      position: static;
      max-width: 100%;
      padding: 0;
      margin-top: 8px;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding: 32px 0 16px;
    &__inner {
      display: flex;
      flex-direction: column;
      gap: 8px;
    }
    &__desc {
      position: static;
      max-width: 100%;
      height: auto;
      order: 1;
      padding: 0;
    }
    &__group {
      border-radius: 12px;
      padding: 20px;
    }
    &__title {
      margin-top: 20px;
    }
    &__subtitle {
      font-size: 14px;
      margin-top: 12px;
      margin-bottom: 12px;
    }
    &__btn {
      width: 100%;
      height: 48px;
      margin-top: 12px;
      span {
        font-size: 14px;
      }
    }
    .swiper-slide {
      min-height: 180px;
    }
    .slider-nav {
      position: absolute;
      left: 50%;
      bottom: 20px;
      transform: translate(-50%, 0);
      background: transparent;
      padding: 0;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
    .swiper-pagination {
      width: 100%;
      &-bullet {
        background: #fff;
      }
    }
  }
  .equipment {
    padding: 16px 0;
    &__cards {
      grid-template-columns: 100%;
      margin-top: 20px;
    }
    &__btn {
      width: 100%;
      height: 48px;
      margin-top: 20px;
      span {
        font-size: 14px;
      }
    }
  }
  .equipment-card {
    min-height: unset;
    grid-column: unset !important;
    padding: 20px;
    &__img {
      max-width: 96px;
      padding-top: 4px;
    }
    &__title {
      font-size: 14px;
    }
    &__subtitle {
      font-size: 14px;
    }
    &__text {
      display: none;
    }
    &__link {
      display: none;
    }
    &__number {
      top: 20px;
      right: 20px;
    }
  }
  .industries {
    padding: 16px 0;
    &__slider {
      margin-top: 20px;
    }
  }
  .industry-card {
    &__desc {
      margin-top: 16px;
    }
  }
  .about {
    padding: 16px 0;
    &__img {
      min-height: 180px;
    }
    &__btn {
      width: 100%;
      height: 48px;
      span {
        font-size: 14px;
      }
    }
  }
  .news {
    padding: 16px 0;
    &2 {
      padding-bottom: 32px;
    }
    &__slider {
      margin-top: 20px;
    }
  }
  .news-card {
    &__tag {
      top: 20px;
      left: 20px;
    }
  }
  .contacts {
    padding: 16px 0 48px;
    .tab {
      height: 40px;
      padding: 5px 16px;
      font-size: 14px;
    }
    &__tabs-head {
      margin-top: 20px;
    }
    &__group {
      padding: 20px;
    }
    &__items {
      gap: 16px;
      margin-top: 20px;
      margin-bottom: 20px;
    }
    &__subtitle {
      font-size: 14px;
    }
    &__subitems {
      gap: 8px;
      margin-top: 12px;
    }
    &__subitem {
      &-icon {
        width: 18px;
      }
      &-text {
        font-size: 14px;
      }
    }
    &__btns {
      width: 100%;
    }
    &__btn {
      height: 48px;
      flex-grow: 1;
      padding: 5px 10px;
      span {
        font-size: 14px;
      }
    }
    &__btn2 {
      width: 48px;
      height: 48px;
    }
    &__btn-map {
      font-size: 14px;
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 450px) {
  .industries {
    .swiper {
      overflow: unset;
    }
  }
  .industry-card {
    min-height: 260px;
    &__img {
      max-width: 96px;
      &:after {
        width: 50px;
        height: 50px;
      }
      &:before {
        width: 50px;
        height: 50px;
      }
    }
  }
}