.search-main {
  padding: 16px 0 116px;
  .swiper {
    overflow: unset;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    width: auto !important;
    height: auto;
  }
  .tab {
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    padding: 5px 16px;
    font-size: 16px;
    line-height: 150%;
    color: #707680;
    cursor: pointer;
    &:first-child {
      border-radius: 8px 0 0 8px;
    }
    &:last-child {
      border-radius: 0 8px 8px 0;
    }
  }
  .tab--active {
    background: #FFE500;
    color: #21262E;
  }
  &__tabs-body {
    margin-top: 32px;
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
  }
  &__item {
    display: flex;
    align-items: start;
    gap: 12px;
    background: #fff;
    border-radius: 12px;
    padding: 24px;
    &-img {
      width: 72px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
  }
  &__title {
    font-size: 16px;
    line-height: 150%;
    span {
      font-weight: 600;
    }
  }
  &__link {
    display: block;
    font-size: 14px;
    line-height: 150%;
    color: #707680;
    margin-top: 4px;
  }
  &__text {
    margin-top: 12px;
    p {
      font-size: 16px;
      line-height: 150%;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:2;
      -webkit-box-orient: vertical;
      span {
        font-weight: 600;
      }
    }
  }
  &__btn {
    height: 56px;
    padding: 5px 24px;
    margin: 32px auto 0;
    span {
      font-size: 16px;
    }
  }
}
.search-mistake {
  max-width: 685px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 100px auto 0;
  &__icon {
    width: 72px;
    img {
      width: 100%;
    }
  }
  &__title {
    margin-top: 16px;
  }
  &__text {
    font-size: 16px;
    line-height: 150%;
    color: #707680;
    margin-top: 12px;
  }
}

@media screen and (max-width: 576px) {
  .search-main {
    padding: 10px 0 32px;
    .tab {
      font-size: 14px;
    }
    &__tabs-body {
      margin-top: 20px;
    }
    &__item {
      gap: 8px;
      padding: 20px;
      &-img {
        width: 56px;
      }
    }
    &__title {
      font-size: 14px;
    }
    &__link {
      font-size: 13px;
    }
    &__text {
      font-size: 14px;
    }
    &__btn {
      width: 100%;
      height: 48px;
      margin-top: 20px;
      span {
        font-size: 14px;
      }
    }
  }
  .search-mistake {
    margin-top: 100px;
    &__icon {
      width: 48px;
    }
    &__title {
      margin-top: 12px;
    }
    &__text {
      font-size: 14px;
      margin-top: 8px;
    }
  }
}