.product-card {
  padding: 16px 0 28px;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 8px;
  }
  &__left {
    width: calc(100% - 403px);
  }
  &__right {
    width: 395px;
  }
  &__group {
    background: #fff;
    border-radius: 12px;
    padding: 24px;
  }
  .grid-group {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    align-items: start;
    gap: 24px;
  }
  .gallery {
    &__big {
      .swiper-slide {
        position: relative;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-bottom: 100%;
        img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border-radius: 12px;
          object-fit: contain;
        }
      }
      .play {
        width: 24px !important;
        height: 26px;
      }
    }
    &__small-wrap {
      display: flex;
      justify-content: space-between;
      align-items: stretch;
      gap: 8px;
      .swiper {
        width: 100%;
      }
      .swiper-slide-thumb-active {
        border: 1px solid #1C468A;
      }
      .swiper-slide__img {
        position: relative;
        width: 100%;
        height: 100%;
        padding-bottom: 100%;
        img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          border-radius: 4px;
          object-fit: contain;
        }
      }
    }
    .play {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 11px;
      img {
        width: 100%;
        border-radius: 0;
      }
    }
    .swiper-button-prev,
    .swiper-button-next {
      position: static;
      width: 32px;
      flex-shrink: 0;
      background: #F2F4F7;
      border-radius: 8px;
    }
  }
  &__desc {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
  }
  .text-overflow {
    height: 216px;
    text-overflow: ellipsis;
    overflow: hidden;
    h5 {
      font-size: 14px;
      line-height: 170%;
      font-weight: 600;
      margin-bottom: 12px;
    }
    p {
      font-size: 14px;
      line-height: 170%;
      &:not(:last-child) {
        margin-bottom: 8px;
      }
    }
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 4px;
      margin-top: 12px;
      li {
        position: relative;
        font-size: 14px;
        line-height: 170%;
        padding-left: 16px;
        &:after {
          content: '';
          position: absolute;
          top: 8px;
          left: 0;
          width: 5px;
          height: 5px;
          background-color: #21262E;
          border-radius: 50%;
        }
      }
    }
  }
  &__show {
    display: inline-block;
    background: transparent;
    padding: 0;
    font-size: 14px;
    line-height: 170%;
    color: #1C468A;
    margin-top: 4px;
    cursor: pointer;
  }
  .show-btn {
    span:nth-child(2) {
      display: none;
    }
    &--active {
      span:nth-child(1) {
        display: none;
      }
      span:nth-child(2) {
        display: block;
      }
    }
  }
  .text-overflow--active {
    height: auto;
    text-overflow: unset;
    overflow: unset;
  }
  &__articles {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: 4px 8px;
    margin-top: 12px;
  }
  &__article {
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F2F4F7;
    border-radius: 8px;
    padding: 4px 12px;
    font-size: 12px;
    line-height: 130%;
    font-weight: 500;
  }
  &__items {
    width: calc(100% - 403px);
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    margin-top: 8px;
  }
  &__item {
    background: #fff;
    border-radius: 12px;
    padding: 24px;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      cursor: pointer;
    }
    .item-show--active {
      .product-card__item-btn {
        transform: rotate(-180deg);
      }
    }
    &-btn {
      width: 18px;
      flex-shrink: 0;
      transition: all 0.3s;
      &:hover {
        color: #21262E;
      }
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
        fill: currentColor;
      }
    }
    &-hidden {
      display: none;
      margin-top: 16px;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 8px;
      p {
        font-size: 16px;
        line-height: 170%;
      }
      ul {
        display: grid;
        grid-template-columns: 100%;
        gap: 8px;
        li {
          position: relative;
          padding-left: 15px;
          font-size: 16px;
          line-height: 170%;
          &:after {
            content: '';
            position: absolute;
            top: 10px;
            left: 0;
            width: 5px;
            height: 5px;
            background: #21262E;
            border-radius: 50%;
          }
        }
      }
    }
  }
  &__price-text {
    font-size: 28px;
    line-height: 140%;
    color: #707680;
  }
  &__price {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px;
  }
  .new-price {
    font-size: 28px;
    line-height: 140%;
    font-weight: 600;
  }
  .old-price__group {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  .old-price {
    font-size: 16px;
    line-height: 150%;
    text-decoration: line-through;
    color: #707680;
  }
  .discount {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #CCFFCE;
    border-radius: 4px;
    padding: 4px 6px;
    font-size: 12px;
    line-height: 130%;
    text-decoration: unset;
    color: #1E8A22;
  }
  .request-discount {
    display: inline-block;
    font-size: 14px;
    line-height: 170%;
    color: #1C468A;
    margin-top: 4px;
  }
  &__btn {
    width: 100%;
    height: 56px;
    margin-top: 24px;
    span {
      font-size: 16px;
    }
  }
  &__btns {
    display: flex;
    align-items: stretch;
    gap: 12px;
    margin-top: 16px;
  }
  &__btn2 {
    height: 40px;
    flex-grow: 1;
    gap: 10px;
    padding: 5px 16px;
    &:hover {
      i svg path {
        fill: #fff;
      }
    }
    span {
      font-size: 16px;
    }
  }
  &__info {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 12px;
    background: #FFE5E5;
    border-radius: 8px;
    padding: 12px;
    margin-top: 16px;
    i {
      width: 20px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 13px;
      line-height: 150%;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    background: #fff;
    border-radius: 12px;
    padding: 24px;
    margin-top: 0;
    li {
      position: relative;
      font-size: 14px;
      line-height: 170%;
      padding-left: 32px;
      &:after {
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        width: 19px;
        height: 19px;
        background-image: url("../img/list-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
  }
  &__banner {
    position: relative;
    background: #21262E;
    border-radius: 12px;
    padding: 24px;
    margin-top: 8px;
    overflow: hidden;
    &-desc {
      max-width: 235px;
    }
    &-title {
      font-size: 16px;
      line-height: 150%;
      color: #fff;
    }
    &-link {
      display: inline-block;
      font-size: 14px;
      line-height: 170%;
      color: #FFE500;
      margin-top: 8px;
    }
    &-img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      width: 147px;
      img {
        width: 100%;
      }
    }
  }
}
.recommendations {
  padding: 28px 0;
  &__slider {
    position: relative;
    margin-top: 32px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    height: auto;
  }
  .swiper-pagination {
    display: none;
  }
}

@media screen and (max-width: 1100px) {
  .product-card {
    @media screen and (min-width: 1024px) {
      &__left {
        width: calc(100% - 368px);
      }
      &__right {
        width: 360px;
      }
      &__btns {
        flex-direction: column;
      }
      &__btn2 {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 1023px) {
  .product-card {
    .grid-group {
      grid-template-columns: 100%;
    }
  }
  .recommendations {
    .slider-nav {
      display: none;
    }
    &__slider {
      padding-bottom: 30px;
    }
    .swiper-pagination {
      bottom: 0;
      display: block;
    }
  }
}

@media screen and (max-width: 850px) {
  .product-card {
    &__inner {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
    }
    &__items {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .product-card {
    padding: 10px 0 16px;
    &__group {
      padding: 16px;
    }
    &__desc {
      gap: 20px;
    }
    &__price-text {
      font-size: 20px;
    }
    .new-price {
      font-size: 20px;
    }
    &__btn {
      height: 48px;
      span {
        font-size: 14px;
      }
    }
    &__btns {
      flex-direction: column;
      margin-top: 12px;
    }
    &__btn2 {
      span {
        font-size: 14px;
      }
    }
    &__list {
      padding: 20px;
    }
    &__banner {
      padding: 20px;
      &-title {
        font-size: 14px;
      }
      &-img {
        width: 130px;
      }
    }
    &__item {
      padding: 20px;
      &-title {
        font-size: 14px;
      }
      &-text {
        p {
          font-size: 14px;
        }
        ul {
          li {
            font-size: 14px;
          }
        }
      }
    }
  }
  .recommendations {
    padding: 16px 0;
    &__slider {
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 359px) {
  .product-card {
    &__banner {
      &-img {
        right: -40px;
      }
    }
  }
}