.industries-main {
  padding: 16px 0 116px;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 24px / 4));
    gap: 8px;
  }
  &__cards2 {
    grid-template-columns: 100% !important;
    .industries-main__card {
      display: flex;
      align-items: center;
      gap: 20px;
      padding: 24px;
    }
    .industries-main__card-img {
      width: 72px;
      height: 72px;
      flex-shrink: 0;
      padding-bottom: 0;
      img {
        position: static;
        border-radius: 8px;
      }
    }
    .industries-main__card-desc {
      padding: 0;
    }
  }
  &__btn {
    height: 56px;
    padding: 5px 24px;
    margin: 32px auto 0;
    span {
      font-size: 16px;
    }
  }
}
.industries-main__card {
  position: relative;
  background: #fff;
  border-radius: 12px;
  &-img {
    position: relative;
    padding-bottom: 55%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px 12px 0 0;
    }
  }
  &-desc {
    padding: 24px;
  }
  &-title {
    font-size: 13px;
    line-height: 150%;
    color: #707680;
  }
  &-text {
    font-size: 16px;
    line-height: 150%;
    margin-top: 8px;
  }
}

@media screen and (max-width: 1023px) {
  .industries-main {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 16px / 3));
    }
  }
  .industries-main__card {
    &-desc {
      padding: 20px;
    }
  }
}

@media screen and (max-width: 768px) {
  .industries-main {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 8px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .industries-main {
    padding: 10px 0 32px;
    &__cards2 {
      .industries-main__card {
        gap: 16px;
        padding: 20px;
      }
      .industries-main__card-img {
        width: 56px;
        height: 56px;
      }
    }
    &__btn {
      width: 100%;
      height: 48px;
      padding: 5px 16px;
      margin-top: 20px;
      span {
        font-size: 14px;
      }
    }
  }
  .industries-main__card {
    &-text {
      font-size: 14px;
      margin-top: 8px;
    }
  }
}

@media screen and (max-width: 450px) {
  .industries-main {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}