.news-main {
  padding: 32px 0 116px;
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 16px / 3));
    gap: 8px;
  }
  &__btn {
    height: 56px;
    padding: 5px 28px;
    margin: 32px auto 0;
    span {
      font-size: 16px;
    }
  }
}
.news-desc {
  padding: 20px 0 28px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 12px;
  }
  &__subgroup {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 12px;
    margin-top: 12px;
  }
  &__tag {
    background: #E4E0FF;
    border-radius: 8px;
    padding: 8px 12px;
    font-size: 12px;
    line-height: 120%;
    font-weight: 500;
    text-transform: uppercase;
    color: #443A85;
  }
  &__date {
    font-size: 14px;
    line-height: 120%;
    color: #707680;
  }
  &__slider {
    position: relative;
    margin-top: 32px;
  }
  .slider-nav {
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: 24px;
    transform: translate(-50%, 0);
  }
  .swiper-slide {
    img {
      width: 100%;
      border-radius: 12px;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    background: #fff;
    border-radius: 12px;
    padding: 24px;
    margin-top: 32px;
    p {
      font-size: 16px;
      line-height: 150%;
    }
    ol {
      display: grid;
      grid-template-columns: 100%;
      gap: 12px;
      padding-left: 24px;
      margin: 0;
      li {
        font-size: 16px;
        line-height: 150%;
        list-style: auto;
      }
    }
  }
  &__img {
    img {
      width: 100%;
      border-radius: 12px;
    }
    span {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      font-size: 13px;
      line-height: 150%;
      color: #707680;
      margin-top: 8px;
    }
  }
  &__img-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F2F4F7;
    border-radius: 12px;
    padding: 24px;
    img {
      max-width: 400px;
    }
  }
}
.send {
  &-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 12px 16px;
    margin-top: 24px;
  }
  &-text {
    font-size: 16px;
    line-height: 150%;
    color: #707680;
  }
  &-items {
    display: flex;
    align-items: center;
    gap: 12px;
  }
  &-item {
    display: flex;
    align-items: center;
    gap: 8px;
    &:hover {
      color: #A7ABB2;
    }
    i {
      max-width: 24px;
      flex-shrink: 0;
    }
    span {
      font-size: 16px;
      line-height: 150%;
    }
  }
}
.other-articles {
  padding: 28px 0 116px;
  &2 {
    max-width: 100%;
    padding-top: 16px;
    .other-articles__items {
      margin-top: 0;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    margin-top: 32px;
  }
}
.article-card {
  background: #fff;
  border-radius: 12px;
  padding: 24px;
  &__date {
    font-size: 13px;
    line-height: 150%;
    color: #707680;
  }
  &__title {
    font-size: 16px;
    line-height: 150%;
    margin-top: 12px;
  }
  &__text {
    font-size: 16px;
    line-height: 150%;
    color: #707680;
    margin-top: 12px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
}

@media screen and (max-width: 768px) {
  .news-main {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 8px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .news-main {
    padding: 20px 0 32px;
    &__cards {
      grid-template-columns: 100%;
    }
    .news-card {
      &:nth-child(9) ~ .news-card {
        display: none;
      }
    }
    &__btn {
      width: 100%;
      height: 48px;
      padding: 5px;
      margin-top: 20px;
      span {
        font-size: 14px;
      }
    }
  }
  .news-desc {
    padding: 16px 0;
    &__slider {
      margin-top: 20px;
    }
    .slider-nav {
      background: transparent;
      padding: 0;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
    .swiper-pagination {
      bottom: 20px;
      &-bullet {
        background: #fff;
      }
    }
    &__text {
      gap: 20px;
      padding: 20px;
      margin-top: 20px;
      p {
        font-size: 14px;
      }
      ol {
        li {
          font-size: 14px;
        }
      }
    }
    &__img {
      span {
        font-size: 12px;
      }
    }
    &__img-wrap {
      padding: 0;
      img {
        max-width: 100%;
      }
    }
  }
  .send {
    &-wrap {
      flex-direction: column;
      align-items: start;
      margin-top: 20px;
    }
    &-items {
      width: 100%;
      display: grid;
      grid-template-columns: 100%;
    }
    &-item {
      height: 40px;
      justify-content: center;
      border: 1px solid #A7ABB2;
      border-radius: 8px;
      padding: 5px 10px;
    }
  }
  .other-articles {
    padding: 16px 0 32px;
    &2 {
      padding-top: 10px;
    }
    &__items {
      margin-top: 20px;
    }
  }
  .article-card {
    padding: 20px;
    &__title {
      font-size: 14px;
      margin-top: 8px;
    }
    &__text {
      font-size: 14px;
      margin-top: 8px;
    }
  }
}